.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #bdd107;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(184, 157, 157);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Styles for the container div */
.container {
  text-align: center;
  margin-top: 50px;
}

/* Styles for the login and details div */
.login-details {
  margin-top: 30px;
}

/* Styles for the logout button */
.logout-btn {
  background-color: #c8d516; /* Red */
  color: rgba(31, 6, 106, 0.652);
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
}

/* Styles for the login button */
.login-btn {
  background-color: #df18c5; /* Green */
  color: rgb(174, 202, 175);
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
}

/* Style for the h1 heading */
.heading {
  font-size: 24px;
  margin-bottom: 200px;
}
/* Styles for the App container */
.App {
  text-align: center;
}

/* Styles for the container-fluid div */
.container-fluid {
  max-width: 10000px; /* Adjust according to your design */
  margin: 0 auto;
  padding: 200px;
  background-color: #bfd709; /* Light gray background */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(7, 164, 248, 0.1); /* Box shadow for a subtle depth effect */
}

/* Styles for the h2 heading */
h2 {
  font-size: 36px;
  margin-bottom: 30px;
}

/* Styles for the Demo component */
.demo-container {
  margin-top: 50px;
}
/* Styles for the h2 heading inside the container-fluid div */
.container-fluid h2 {
  font-size: 50px;
  margin-bottom: 30px;
  color: #3b09f0ba; /* Coral color */
}
